body {
    background-color: grey;
}
h1 {    
    text-shadow: -1px 0 #fff, 0 1px #fff, 1px 0 #fff, 0 -1px #fff;
}

div {
    margin: 0 auto;
    text-align: center;
    display: inline-block;
}
ul {
    text-align: center;
    font-size: 1.3em;
    color: black;
}
img {
    width: 220px;
    height: 320px;
    border: 1px solid white;
}
.active {
    background-color: blueviolet;
    color: white;
}
li {
    display: inline-block;
    list-style: none;
    padding: 50px;
}
a:visited {
    text-decoration: none;
    color: white;
}
a:link {
    text-decoration: none;
    color: white;
}
p:first-of-type {
    text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
    font-size: 2.0em;
    
}
p {
    color: white;
}
.content div {
    margin-right: 80px;
}
